___CSS_LOADER_EXPORT___.push([module.id, `.calendar-inp_wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  height: 51px;
  padding: 5px;
  background-color: #f6f6f6;
  border: 0.5px solid #969fa8;
}

.calendar.selected .calendar-label,
.calendar.selected .calendar-inp,
.calendar.selected .calendar-icon,
.calendar.selected .calendar-delete {
  background-color: #D4F6FF;
}

.calendar-inp_wrapper .calendar-label {
  display: flex;
  align-items: center;
  flex: 0 0 auto;

  width: max-content;
  height: 100%;
  padding: 0 5px 0 23px;

  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 19px;
  color: #434c4e;
  text-transform: uppercase;
}

.calendar-inp_wrapper .calendar-inp {
  flex: 1 0 auto;
  max-width: 219px;
  /* width: 100%; */
  height: 100%;
  background-color: transparent;
  border: none;
  outline: none;

  font-family: "Roboto", sans-serif;
  font-weight: 900;
  font-size: 19px;
  color: #000;
  text-transform: uppercase;
}
.calendar.selected .calendar-inp{
  max-width: 177px;
}

.calendar-inp_wrapper .calendar-inp::placeholder, .calendar input[type="date"] {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 19px;
  color: #b2b4b4;
  text-transform: uppercase;
}

.calendar-inp_wrapper .calendar-icon{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calendar-inp_wrapper .calendar-delete {
  display: none;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto;
  width: 34px;
  height: 100%;
  margin-left: 4px;
  cursor: pointer;
}

.calendar.selected .calendar-delete{
  display: flex;
}

.calendar input[type="date"]{
  display: none;
}
.calendar input[type="date"].active{
  display: block;
}

.calendar input[type="date"]::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/calenda-input.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,mBAAmB;EACnB,WAAW;EACX,YAAY;EACZ,YAAY;EACZ,yBAAyB;EACzB,2BAA2B;AAC7B;;AAEA;;;;EAIE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,cAAc;;EAEd,kBAAkB;EAClB,YAAY;EACZ,qBAAqB;;EAErB,iCAAiC;EACjC,gBAAgB;EAChB,eAAe;EACf,cAAc;EACd,yBAAyB;AAC3B;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,iBAAiB;EACjB,YAAY;EACZ,6BAA6B;EAC7B,YAAY;EACZ,aAAa;;EAEb,iCAAiC;EACjC,gBAAgB;EAChB,eAAe;EACf,WAAW;EACX,yBAAyB;AAC3B;AACA;EACE,gBAAgB;AAClB;;AAEA;EACE,iCAAiC;EACjC,gBAAgB;EAChB,eAAe;EACf,cAAc;EACd,yBAAyB;AAC3B;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,cAAc;EACd,WAAW;EACX,YAAY;EACZ,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;AACf;AACA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,wBAAwB;AAC1B","sourcesContent":[".calendar-inp_wrapper {\n  display: flex;\n  align-items: center;\n  width: 100%;\n  height: 51px;\n  padding: 5px;\n  background-color: #f6f6f6;\n  border: 0.5px solid #969fa8;\n}\n\n.calendar.selected .calendar-label,\n.calendar.selected .calendar-inp,\n.calendar.selected .calendar-icon,\n.calendar.selected .calendar-delete {\n  background-color: #D4F6FF;\n}\n\n.calendar-inp_wrapper .calendar-label {\n  display: flex;\n  align-items: center;\n  flex: 0 0 auto;\n\n  width: max-content;\n  height: 100%;\n  padding: 0 5px 0 23px;\n\n  font-family: \"Roboto\", sans-serif;\n  font-weight: 300;\n  font-size: 19px;\n  color: #434c4e;\n  text-transform: uppercase;\n}\n\n.calendar-inp_wrapper .calendar-inp {\n  flex: 1 0 auto;\n  max-width: 219px;\n  /* width: 100%; */\n  height: 100%;\n  background-color: transparent;\n  border: none;\n  outline: none;\n\n  font-family: \"Roboto\", sans-serif;\n  font-weight: 900;\n  font-size: 19px;\n  color: #000;\n  text-transform: uppercase;\n}\n.calendar.selected .calendar-inp{\n  max-width: 177px;\n}\n\n.calendar-inp_wrapper .calendar-inp::placeholder, .calendar input[type=\"d